body {
  font-family: 'Arial', sans-serif;
  background: url('./pexels-pixabay-326333.jpg') no-repeat center center fixed; /* Background image */
  background-size: cover; /* Make sure the background covers the entire screen */
  background-color: rgba(0, 0, 0, 0.5); /* Transparent overlay over the background image */
  backdrop-filter: blur(5px); /* Optional blur to enhance readability */
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.container {
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent container */
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2); /* Subtle shadow for depth */
  max-width: 500px;
  width: 100%;
  text-align: center;
  backdrop-filter: blur(8px); /* Additional blur inside container */
}

h1 {
  color: #ad33ff; /* Unique header color */
  margin-bottom: 20px;
}

.translator-container {
  display: flex;
  flex-direction: column;
}

.field {
  margin-bottom: 15px;
  text-align: left;
  color: white;
}

label {
  display: block;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 5px;
}

select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 14px;
}

.input-text {
  width: 100%;
  height: 100px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 20px;
  font-size: 14px;
  resize: none;
}

.translate-btn {
  background-color: #724caf; /* Unique button color */
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, box-shadow 0.3s ease, transform 0.3s ease;
}

.translate-btn:hover {
  background-color: #222923; /* Darker color on hover */
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.3); /* Add shadow on hover */
  transform: translateY(-2px); /* Slightly lift the button */
}

.error-message {
  color: #e74c3c; /* Red color for error message */
  font-size: 14px;
  margin-top: 10px;
}

.result {
  margin-top: 20px;
}

.result h2 {
  color: #414641; /* Match button color */
  font-size: 18px;
}

.result p {
  background-color: rgba(255, 255, 255, 0.8); /* Semi-transparent background for translated text */
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  font-size: 16px;
}

/* Media Query for smaller devices */
@media (max-width: 600px) {
  .container {
    padding: 20px;
    max-width: 90%;
  }

  h1 {
    font-size: 24px;
  }

  .translate-btn {
    padding: 10px;
    font-size: 14px;
  }

  .input-text {
    height: 80px;
    font-size: 12px;
  }
}
